*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Lato", sans-serif;
  line-height: 1.7;
  color: #777;
  padding: 3rem;
}

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }
  80% {
    transform: translate(1.5rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translateX(10rem);
  }
  80% {
    transform: translate(-1.5rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(3rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
.text-align--center {
  text-align: center;
}

.margin-bottom--medium {
  margin-bottom: 4rem !important;
}

.margin-bottom--big {
  margin-bottom: 8rem;
}

.margin-bottom--huge {
  margin-bottom: 10rem !important;
}

.btn, .btn:link, .btn:visited {
  text-decoration: none;
  text-transform: uppercase;
  display: inline-block;
  padding: 1.5rem 4rem;
  border-radius: 10rem;
  transition: transform 0.2s, box-shadow 0.2s;
  position: relative;
  font-size: 1.6rem;
  border: none;
  cursor: pointer;
}
.btn--white {
  background-color: #fff;
  color: #777;
}
.btn--white::after {
  background-color: #fff;
}
.btn--green {
  background-color: #55c57a;
  color: #fff;
}
.btn--green::after {
  background-color: #55c57a;
}
.btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.3);
}
.btn:hover::after {
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}
.btn:active {
  transform: translateY(-1px);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.6);
}
.btn::after {
  content: "";
  display: inline-block;
  z-index: -1;
  width: 100%;
  height: 100%;
  border-radius: 10rem;
  position: absolute;
  top: 0;
  left: 0;
  transition: transform 0.4s, opacity 0.4s;
}

.btn-text:link, .btn-text:visited {
  text-decoration: none;
  color: #55c57a;
  display: inline-block;
  padding: 3px;
  border-bottom: 1px solid #55c57a;
  transition: all 0.2s;
  font-size: 1.6rem;
}
.btn-text:hover {
  background-color: #55c57a;
  color: #fff;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15);
  transform: translateY(-2px);
}
.btn-text:active {
  transform: translateY(0);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}

.title {
  font-size: 3.5rem;
  text-transform: uppercase;
  font-weight: 700;
  display: inline-block;
  background-image: linear-gradient(to right, #7ed56f, #28b485);
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  letter-spacing: 2px;
  transition: all 0.2s;
}
.title:hover {
  transform: skewY(2deg) skewX(15deg) scale(1.1);
  text-shadow: 0.5rem 1rem 2rem rgba(0, 0, 0, 0.2);
}

.title-secondary {
  font-size: 1.6rem;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 2rem;
}

.paragraph {
  font-size: 1.6rem;
  margin-bottom: 3rem;
}

.composite {
  position: relative;
}
.composite__img {
  width: 55%;
  box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.4);
  border-radius: 2px;
  position: absolute;
  z-index: 5;
  transition: all 0.2s;
  outline-offset: 2rem;
}
.composite__img--p1 {
  top: -2rem;
  left: 0;
}
.composite__img--p2 {
  top: 2rem;
  right: 0;
}
.composite__img--p3 {
  top: 10rem;
  left: 20%;
}
.composite__img:hover {
  z-index: 6;
  transform: scale(1.05) translateY(-0.5rem);
  box-shadow: 0 2.5rem 4rem rgba(0, 0, 0, 0.5);
  outline: solid 1.5rem #55c57a;
}
.composite:hover .composite__img:not(:hover) {
  transform: scale(0.95);
}

.feature {
  background-color: rgba(255, 255, 255, 0.8);
  font-size: 1.5rem;
  padding: 2.5rem;
  border-radius: 3px;
  text-align: center;
  box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
  transition: transform 0.3s;
}
.feature:hover {
  transform: translateY(-1.5rem) scale(1.03);
}
.feature__icon {
  font-size: 6rem;
  margin-bottom: 0.5rem;
  color: transparent;
  background-image: linear-gradient(to right, #7ed56f, #28b485);
  background-clip: text;
  -webkit-background-clip: text;
}

.tour {
  height: 52rem;
  position: relative;
  perspective: 150rem;
  -moz-perspective: 150rem;
}
.tour__side {
  height: 52rem;
  position: absolute;
  width: 100%;
  transition: transform 0.8s ease;
  backface-visibility: hidden;
  border-radius: 3px;
  overflow: hidden;
  box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
}
.tour__side--back {
  transform: rotateY(180deg);
}
.tour__side--yellow {
  background-image: linear-gradient(to right bottom, #ffb900, #ff7730);
}
.tour__side--green {
  background-image: linear-gradient(to right bottom, #7ed56f, #28b485);
}
.tour__side--blue {
  background-image: linear-gradient(to right bottom, #2998ff, #5643fa);
}
.tour:hover .tour__side--front {
  transform: rotateY(-180deg);
}
.tour:hover .tour__side--back {
  transform: rotateY(0);
}
.tour__img {
  height: 23rem;
  clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
  background-blend-mode: screen;
  background-size: cover;
  border-radius: 3px 3px 0 0;
}
.tour__img--sea {
  background-image: linear-gradient(to right bottom, #ffb900, #ff7730), url(../img/nat-5.jpg);
}
.tour__img--forest {
  background-image: linear-gradient(to right bottom, #7ed56f, #28b485), url(../img/nat-6.jpg);
}
.tour__img--snow {
  background-image: linear-gradient(to right bottom, #2998ff, #5643fa), url(../img/nat-7.jpg);
}
.tour__title {
  font-size: 2.8rem;
  font-weight: 300;
  position: absolute;
  top: 12rem;
  right: 2rem;
  text-transform: uppercase;
  width: 75%;
  color: #fff;
  text-align: right;
}
.tour__span {
  padding: 1rem 1.5rem;
  -webkit-box-decoration-break: clone;
  box-decoration-break: clone;
}
.tour__span--yellow {
  background-image: linear-gradient(to right bottom, rgba(255, 185, 0, 0.85), rgba(255, 119, 48, 0.85));
}
.tour__span--green {
  background-image: linear-gradient(to right bottom, rgba(126, 213, 111, 0.85), rgba(40, 180, 133, 0.85));
}
.tour__span--blue {
  background-image: linear-gradient(to right bottom, rgba(41, 152, 255, 0.85), rgba(86, 67, 250, 0.85));
}
.tour__list ul {
  font-size: 1.5rem;
  width: 80%;
  margin: 0 auto;
  list-style-type: none;
  text-align: center;
}
.tour__list ul li {
  padding: 1rem;
}
.tour__list ul li:not(:last-child) {
  border-bottom: 1px solid #eee;
}
.tour__back {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  text-align: center;
}
.tour__price-box {
  color: #fff;
  margin-bottom: 8rem;
}
.tour__only {
  font-size: 1.4rem;
}
.tour__price {
  font-size: 6rem;
  font-weight: 100;
}

.story {
  width: 75%;
  box-shadow: 0 3rem 6rem rgba(0, 0, 0, 0.1);
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 3px;
  padding: 6rem;
  padding-left: 9rem;
  margin: 0 auto;
  font-size: 1.6rem;
  transform: skewX(-12deg);
}
.story__shape {
  width: 15rem;
  height: 15rem;
  float: left;
  clip-path: circle(50% at 50% 50%);
  shape-outside: circle(50% at 50% 50%);
  transform: translateX(-3rem) skewX(12deg);
  position: relative;
}
.story__title {
  margin-bottom: 1.5rem;
  text-transform: uppercase;
}
.story__text {
  transform: skewX(12deg);
}
.story__img {
  height: 100%;
  transform: translateX(-4rem) scale(1.4);
  transition: all 0.5s;
}
.story__caption {
  text-transform: uppercase;
  position: absolute;
  top: 50%;
  left: 50%;
  color: #fff;
  font-size: 1.7rem;
  transform: translate(-50%, 20%);
  text-align: center;
  opacity: 0;
  transition: all 0.5s;
}
.story:hover .story__caption {
  transform: translate(-50%, -50%);
  opacity: 1;
}
.story:hover .story__img {
  transform: translateX(-4rem) scale(1);
  filter: blur(3px) brightness(80%);
}

.bg-video {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  opacity: 0.15;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.bg-video__content {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.book {
  background-image: linear-gradient(105deg, rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0.9) 50%, transparent 50%), url(../img/nat-10.jpg);
  background-size: cover;
  border-radius: 3px;
  box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.2);
}
.book__form {
  width: 50%;
  padding: 6rem;
}

.form__field:not(:last-child) {
  margin-bottom: 2rem;
}
.form__input {
  font-size: 1.5rem;
  padding: 1.5rem 2rem;
  border-radius: 2px;
  background-color: rgba(255, 255, 255, 0.5);
  border: none;
  outline: none;
  width: 90%;
  display: block;
  font-family: inherit;
  border-bottom: 3px solid transparent;
  transition: all 0.3s;
}
.form__input:focus {
  border-bottom: 3px solid #55c57a;
}
.form__input:focus:invalid {
  border-bottom: 3px solid #ff7730;
}
.form__input::-webkit-input-placeholder {
  color: #999;
}
.form__input:placeholder-shown + .form__label {
  visibility: hidden;
  opacity: 0;
  transform: translateY(-4rem);
}
.form__label {
  font-size: 1.2rem;
  font-weight: 700;
  margin: 0.7rem 0 0 2rem;
  display: block;
  transition: all 0.3s;
}
.form__radio-group {
  display: inline-block;
  width: 49%;
}
.form__radio-label {
  font-size: 1.6rem;
  position: relative;
  padding-left: 4.5rem;
  cursor: pointer;
}
.form__radio-input {
  display: none;
}
.form__radio-input:checked + .form__radio-label .form__radio-btn::after {
  background-color: #55c57a;
}
.form__radio-btn {
  position: absolute;
  display: inline-block;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  top: -0.4rem;
  left: 0;
  border: 5px solid #55c57a;
}
.form__radio-btn::after {
  content: "";
  display: block;
  width: 1.3rem;
  height: 1.3rem;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s;
}

.navigation__btn {
  display: inline-block;
  position: fixed;
  width: 7rem;
  height: 7rem;
  top: 6rem;
  right: 6rem;
  z-index: 200;
  background-color: #fff;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.1);
}
.navigation__checkbox {
  display: none;
}
.navigation__bg {
  position: fixed;
  width: 6rem;
  height: 6rem;
  top: 6.5rem;
  right: 6.5rem;
  background-image: radial-gradient(#7ed56f, #28b485);
  border-radius: 50%;
  z-index: 150;
  transition: transform 0.8s cubic-bezier(0.83, 0, 0.17, 1);
}
.navigation__nav {
  position: fixed;
  height: 100vh;
  opacity: 0;
  top: 0;
  left: 0;
  width: 0;
  z-index: 180;
  transition: all 0.8s cubic-bezier(0.68, -0.6, 0.32, 1.6);
}
.navigation__list {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  list-style: none;
}
.navigation__item {
  margin: 1rem;
}
.navigation__link:link, .navigation__link:visited {
  color: #fff;
  text-decoration: none;
  font-size: 3rem;
  font-weight: 300;
  padding: 1rem 2rem;
  display: inline-block;
  background-image: linear-gradient(120deg, transparent 0%, transparent 50%, #fff 50%);
  background-size: 230%;
  transition: all 0.4s;
}
.navigation__link:hover, .navigation__link:active {
  color: #55c57a;
  background-position: 100%;
  transform: translateX(1rem);
}
.navigation__link span {
  margin-right: 1.5rem;
}
.navigation__checkbox:checked ~ .navigation__bg {
  transform: scale(80);
}
.navigation__checkbox:checked ~ .navigation__nav {
  width: 100%;
  opacity: 1;
}

.header {
  height: 95vh;
  background-image: linear-gradient(to right bottom, rgba(126, 213, 111, 0.8), rgba(40, 180, 133, 0.8)), url(../img/hero.jpg);
  background-size: cover;
  background-position: top;
  position: relative;
  clip-path: polygon(0 0, 100% 0, 100% 75vh, 0 100%);
}
.header__logo-box {
  position: absolute;
  top: 4rem;
  left: 4rem;
}
.header__logo {
  height: 3.5rem;
}
.header__btn {
  animation: moveInBottom 0.5s ease-out 0.75s;
  animation-fill-mode: backwards;
}
.header__content {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%);
  text-align: center;
}
.header__title {
  color: #fff;
  text-transform: uppercase;
  margin-bottom: 6rem;
  backface-visibility: hidden;
}
.header__title--sup {
  display: block;
  font-size: 6rem;
  letter-spacing: 3.5rem;
  font-weight: 400;
  animation: moveInLeft 1s ease-out;
}
.header__title--sub {
  display: block;
  font-size: 2rem;
  letter-spacing: 1.75rem;
  animation: moveInRight 1s ease-out;
}

.row {
  margin: 0 auto;
  width: 114rem;
}
.row:not(:last-child) {
  margin-bottom: 6rem;
}
.row::after {
  content: "";
  display: table;
  clear: both;
}

[class^=col-] {
  float: left;
}
[class^=col-]:not(:last-child) {
  margin-right: 6rem;
}

.col-1-of-2 {
  width: calc((100% - 6rem) / 2);
}

.col-1-of-3 {
  width: calc((100% - 2 * 6rem) / 3);
}

.col-2-of-3 {
  width: calc(2 * ((100% - 2 * 6rem) / 3) + 6rem);
}

.col-1-of-4 {
  width: calc((100% - 3 * 6rem) / 4);
}

.col-2-of-4 {
  width: calc(2 * ((100% - 3 * 6rem) / 4) + 6rem);
}

.col-3-of-4 {
  width: calc(3 * ((100% - 3 * 6rem) / 4) + 2 * 6rem);
}

.about {
  background-color: #f7f7f7;
  padding: 25rem 0;
  margin-top: -20vh;
}
.about__title-wrap {
  text-align: center;
  margin-bottom: 8rem;
}

.features {
  padding: 20rem 0;
  background-image: linear-gradient(to right bottom, rgba(126, 213, 111, 0.8), rgba(40, 180, 133, 0.8)), url(../img/nat-4.jpg);
  background-size: cover;
  transform: skewY(-7deg);
  margin-top: -12rem;
}
.features > * {
  transform: skewY(7deg);
}

.tours {
  margin-top: -10rem;
  padding: 25rem 0 15rem;
  background-color: #f7f7f7;
  text-align: center;
}
.tours__title-wrap {
  text-align: center;
  margin-bottom: 8rem;
}
.tours__btn {
  margin-top: 5rem;
}

.stories {
  padding: 15rem 0;
  position: relative;
}

.booking {
  padding: 15rem 0;
  background-image: linear-gradient(to right bottom, #7ed56f, #28b485);
}

.footer {
  padding: 10rem 0;
  background-color: #333;
  font-size: 1.4rem;
  color: #f7f7f7;
}
.footer__logo-box {
  margin-bottom: 8rem;
  text-align: center;
}
.footer__logo {
  width: 15rem;
  height: auto;
}
.footer__navigation {
  border-top: 1px solid #777;
  padding-top: 2rem;
  display: inline-block;
}
.footer__list {
  list-style: none;
}
.footer__item {
  display: inline-block;
}
.footer__item:not(:last-child) {
  margin-right: 1.5rem;
}
.footer__link:link, .footer__link:visited {
  display: inline-block;
  transition: 0.2s;
  color: #f7f7f7;
  text-decoration: none;
  background-color: #333;
  text-transform: uppercase;
}
.footer__link:hover, .footer__link:active {
  transform: rotate(5deg) scale(1.3);
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.4);
  color: #55c57a;
}
.footer__copyright {
  border-top: 1px solid #777;
  padding-top: 2rem;
  width: 80%;
  float: right;
}